import React, { PureComponent } from "react";
import AuthorDetails from "./AuthorDetails";
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import Network from "../../Elements/Network/Network";
import { themeImpressionTracking } from "../../../../utils";
import { isTagShop, isTagbox, suiteTrack } from "../../../../actions/api";

export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.cardSize = React.createRef();
  }

  componentDidMount() {
    const { itemData, itemIndex, onClickPopUpSlider } = this.props
    const container = document.querySelector('.tb_gp_post_container');
    setTimeout(() => {
      if (this?.cardSize?.current?.offsetWidth < 250) {
        container.classList.add('tb_gp_post_wrapper2x');
      }
      if (this?.cardSize?.current?.offsetWidth < 120) {
        container.classList.add('tb_gp_post_wrapper1x');
      }
      themeImpressionTracking(this.cardSize)
    }, 100);
    if (itemData.isPopUp) {
      onClickPopUpSlider(itemIndex, itemData)
    }
  }

  render() {
    const { itemData, personalization, adjustWidth, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId } = this.props;
    const cardSize = {
      width: `${adjustWidth}%`, padding: personalization.padding / 2
    };
    const mediaType = itemData.type !== 1 ? true : false;
    const isYoutubePost = (itemData.network.id === 7) ? true : false
    const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'
    const overlay = isTagbox ? 'tb_gp_color_overlay' : 'tb_gp_post_overlay-' + themeMode
    const overlayStyle = { backgroundColor: isTagbox ? ThemeRule.cardColor : '' }

    return (
      <div id={`tb-gp-post-${itemData.id}`} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId} className={`tb_gp_post_wrapper`} style={cardSize} tb-network={itemData.network.id} tb-product={itemData.isShopIcon ? 1 : itemData.isHotspot ? 2 : 0} tabIndex="0" role="article" aria-label={`Post ${parseInt(itemIndex) + 1},${itemData.content}`} ref={this.cardSize}>

        <div className={`tb_gp_post_in`} onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-label="Popup" tabIndex={"0"}>
          <div className="tb_gp_post_media_wrapp" role="button" tabIndex="0" aria-label="Pop Up Button">
            {itemData.share.status ? <ShareOnHover share={itemData.share} shareClass={'tb_gp_share_container'} item={itemData} wallID={wallID} ownerId={ownerId} /> : null}
            {mediaType ? <Media key={`img_gly_${itemData.id}`} itemData={itemData} wallID={wallID} themeID={personalization.widgetTheme} ownerId={ownerId} /> : ''}

            {!isYoutubePost ?
              <div className={`tb_gp_contant_`}>
                <div className="tb_gp_contant__in">
                  <div className="tb_gp_content__">
                    <AuthorDetails adjustWidth={adjustWidth} postTime={itemData.createdAt} network={itemData.network} ownerId={ownerId} author={itemData.author} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />
                    {!ThemeRule.hideContent || itemData.type == 1 ? <PostContent item={itemData} contentClass="tb_gp_content" content={itemData.content} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /> : ''}
                  </div>
                  <div className={`tb_gp_post_overlay ${overlay}`} style={overlayStyle}><div></div></div>
                </div>
              </div>
              : ''}
            <div className="tb_gp_social_">
              {!isYoutubePost ? <Network networkClass={'tb_gp_social__ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /> : ''}
            </div>

          </div>
        </div>
      </div>
    );
  }
}