import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
import Seprator from "../../Elements/Seprator/Seprator";
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  return (
    <div className="tb_gp_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
      <div className="tb_gp_author">
        {postAuthor ? <Author author={author} authorClass="tb_gp_author_profile" network={network} /> : ''}
        <div className="tb_gp_author_info">
          {postAuthor ? <div className={`tb_gp_authorname`}>{author.name}</div> : ''}
          <div className={`tb_gp_post_info${!authorSeprator ? ' tb_gp_post_info__' : ''}`}>
            {postAuthor ? <div className="tb_gp_username">{username}</div> : ''}
            {authorSeprator ? <Seprator sepClass={`tb_gp_seprator`} /> : ''}
            {personalization.postTime ? <Time postTime={postTime} timeClass={`tb_gp_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /> : ''}
          </div>
        </div>
      </div>
    </div>
  );
}
export default AuthorDetails
