import React, { PureComponent } from "react";
import Masonry from 'react-masonry-component';
import Card from "./Elements/Card";
import "./scss/GalleryPhoto.scss";
class GalleryPhoto extends PureComponent {

  state = {
    postData: [],
    windowWidth: window.innerWidth,
    containerWidth: 0,
    loadData: 0
  }
  componentDidMount() {
    const { postData, renderId, loadBranding } = this.props;

    setTimeout(() =>
      this.setState({ postData, loadData: 1 }, () => this.onLoadMasonry())
      , 100)

    if (renderId.renderId) {
      this.setState({
        windowWidth: document.getElementById(renderId.renderId).clientWidth
      })
    }

     loadBranding()
  }
  
  UNSAFE_componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    this.setState({ postData, loadData: 1 })
  }

  calculationData = (length, cardNumber1, columnCount, totalPostBestFit) => {
    if (length > totalPostBestFit && length > (totalPostBestFit + columnCount)) return { totalPostBestFit: (totalPostBestFit + columnCount), isLastRow: true };
    else return { totalPostBestFit: totalPostBestFit, isLastRow: false };
  }
  render() {
    const { postData, completeDataObject, adjustWidth, ThemeStyle, ThemeInfo, clickToShowPopUp, cardNumber, onClickToCTA, onClickPopUpSlider } = this.props;
    const { windowWidth, loadData } = this.state;

    var totalPostBestFit = { totalPostBestFit: 0, isLastRow: false };;
    let columnCountValue = ThemeStyle.column.desktop === 0 ? Math.trunc(cardNumber) > 5 ? 5 : Math.trunc(cardNumber) : ThemeStyle.column.desktop;
    if (postData.length > parseInt(cardNumber)) {
      let cardNumber1 = parseInt(cardNumber);
      if (columnCountValue === 4) {
        cardNumber1 = 9;
      }
      else if (columnCountValue === 2) {
        cardNumber1 = 3;
      }
      else if (columnCountValue === 3) {
        cardNumber1 = 6;
      }
      else if (columnCountValue === 5) {
        cardNumber1 = 12;
      }
      if (windowWidth < 768) cardNumber1 = parseInt(cardNumber);

      let bestFitEachRow = postData.length / parseInt(cardNumber1);
      totalPostBestFit = (parseInt(cardNumber1) * parseInt(bestFitEachRow));

      totalPostBestFit = this.calculationData(postData.length, cardNumber1, columnCountValue, totalPostBestFit)
    }

    let columnCount = ThemeStyle.column.desktop === 0 ? Math.trunc(cardNumber) > 5 ? 5 : Math.trunc(cardNumber) + 1 : ThemeStyle.column.desktop + 1;
    return (

      <Masonry
        className={`tb_gp_post_container`}
        tabIndex='0'
        aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed"
        elementType={'div'}
        options={{
          transitionDuration: 0,
          percentPosition: true,
          itemSelector: '.tb_gp_post_wrapper',
          resize: true,
        }}
        disableImagesLoaded={false}
        updateOnEachImageLoad={false}
        imagesLoadedOptions={{}}
        enableResizableChildren={true}>
        {loadData ?
          ((postData && postData.length > 0)) && postData.map((item, index) => {
            const cardData = completeDataObject[item];

            let actualPostWidth = adjustWidth;
            if (columnCountValue === 3 && columnCount === (index + 1)) {
              actualPostWidth = (adjustWidth * 2);
              let nextCount = columnCount + (columnCountValue * 2);
              columnCount = nextCount;
            }
            else if (columnCountValue === 2 && columnCount === (index + 1)) {
              actualPostWidth = (adjustWidth * 2);
              let nextCount = columnCount + (columnCountValue + 1);
              columnCount = nextCount;
            }
            else if (columnCountValue === 4 && columnCount === (index + 1)) {
              actualPostWidth = (adjustWidth * 2);
              let nextCount = columnCount + ((columnCountValue * 2) + 1);
              columnCount = nextCount;
            }
            else if (columnCountValue === 5 && columnCount === (index + 1)) {
              actualPostWidth = (adjustWidth * 2);
              let nextCount = columnCount + ((columnCountValue * 2) + 2);
              columnCount = nextCount;
            }

            return parseInt(totalPostBestFit.totalPostBestFit) >= parseInt((index + 1)) ? <Card ownerId={ThemeInfo.ownerId} itemData={cardData} key={index} itemIndex={index} adjustWidth={actualPostWidth}  clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} />
              : parseInt(totalPostBestFit.totalPostBestFit) === 0 ? <Card ownerId={ThemeInfo.ownerId} itemData={cardData} key={`galry_${cardData.id}`} itemIndex={index} adjustWidth={actualPostWidth} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} /> : null

          })
          : null}
      </Masonry>
    );
  }
}

export default GalleryPhoto