import React, {PureComponent } from "react";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Image from "../../Elements/Media/Image";
export default class Media extends PureComponent {

  render() {
    const { itemData, wallID, themeID, ownerId } = this.props;
    return (
      <div className="tb_gp_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
        <MediaIcon itemData={itemData} IconClass="tb_gp_media_icon" position={1} show={true}  observer={true} />
        <Image ImageClass="tb_gp_image" data={itemData} wallID={wallID} themeID={themeID} ownerId={ownerId} size={100} observer={true} />
      </div>
    );
  }
}